import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
  @font-face {
    font-family: wipeout;
    src: url(data:application/x-font-woff;charset=utf-8;base64,),
      format("woff");
    font-weight: normal;
    font-style: normal;
  }

  :root {
    --color-black: black;
    --color-orange: #f9b714;
    --color-white: white;
  }


  body {
    position: relative;
    margin: 0;
    padding: 0;
    font-family: Open-Sans, Helvetica, Sans-Serif;
    background-color: var(--color-black);
    width: 100%;
    height: 100%;
  }

  h3 {
    font-family: wipeout;
    font-size: 1.5rem;
    color: var(--color-orange);
    margin: 0 0 1rem 0;
    width: max-content;
  }

  p {
    font-family: wipeout;
    color: var(--color-white);
    margin: 0;
    width: max-content;
  }
`;

export default GlobalStyle;
